// src/components/SnowEffect.jsx
import { useEffect } from 'react';

function SnowEffect() {
  useEffect(() => {
    console.log('  ...');
    
    //   : /snowFlakes/snowFlakes/
    const basePath = process.env.PUBLIC_URL || '';
    const snowPath = `${basePath}/snowFlakes/snowFlakes`;
    
    console.log('Snow path:', snowPath);
    
    // 1.  CSS
    const cssUrl = `${snowPath}/snow.min.css`;
    console.log('CSS URL:', cssUrl);
    
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = cssUrl;
    link.onload = () => console.log(' CSS ');
    link.onerror = () => console.error('  CSS');
    document.head.appendChild(link);
    
    // 2.  JS
    const jsUrl = `${snowPath}/Snow.js`;
    console.log('JS URL:', jsUrl);
    
    const script = document.createElement('script');
    script.src = jsUrl;
    script.async = true;
    
    script.onload = () => {
      console.log(' JS ');
      console.log('window.Snow:', typeof window.Snow);
      
      //     
      setTimeout(() => {
        if (window.Snow && typeof window.Snow === 'function') {
          try {
            new window.Snow();
            console.log('  !');
          } catch (error) {
            console.error(' :', error);
          }
        } else {
          console.warn(' window.Snow  ');
          console.log(' :', Object.keys(window).filter(k => k.toLowerCase().includes('snow')));
        }
      }, 500);
    };
    
    script.onerror = () => {
      console.error('   JS');
      
   
    };
    
    document.body.appendChild(script);
    
    return () => {
      //   
      const snowElements = document.querySelectorAll('.snowflake, .snow-container');
      snowElements.forEach(el => el.remove());
    };
  }, []);
  
  return null;
}

export default SnowEffect;  