import React, { useState } from "react";
import { AuthService } from "../services/auth.service";
import { toast } from "react-toastify";
import { useAppDispatch } from "../store/hooks";
import { login } from "../store/user/userSlice";
import { Link, useNavigate } from "react-router-dom";
import { setTokenToLocalStorage } from "../helpers/localStorage.helper";

function Section() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLogin, setIsLogin] = useState("");
  const [confPassword, setConfPassword] = useState("");
  const [passwordMatch, setPasswordMatch] = useState(true);
  const [error, setError] = useState(false);
  const [submitted, setSubmitted] = useState(false);

  const dispatch = useAppDispatch(); //  dispatch 
  const navigate = useNavigate();

  const regHandler = async (event) => {
    event.preventDefault();

    //  
    if (!email || !password || !confPassword) {
      setError(true);
      return;
    }

    if (password !== confPassword) {
      setPasswordMatch(false);
      return;
    }

    setPasswordMatch(true);
    setError(false);

    try {
      // 1. 
      const regData = await AuthService.registration({ email, password });

      if (regData) {
        toast.success(" .");

        // 2.     
         try {
      
              const data = await AuthService.login({ email, password });
        
              if (data) {
                setTokenToLocalStorage("token", data.token);
        
                //    localStorage
                localStorage.setItem(
                  "user",
                  JSON.stringify({
                    userId: data.userId,
                    email: data.email,
                  })
                );
        
                dispatch(
                  login({
                    userId: data.userId,
                    email: data.email,
                    token: data.token,
                    avatar: data.avatar,
                  })
                );
        
                toast.success("   .");
                navigate("/");
              }
        } catch (loginError) {
          toast.error(
            "  : " +
              loginError.response?.data?.message
          );
        }
      }
    } catch (err) {
      const error = err.response?.data?.message || " ";
      toast.error(error.toString());
    }
  };

  return (
    <>
      <section
        className="content-reg"
        style={{ display: submitted ? "" : "none" }}
      >
        <div
          className="messages"
          style={{ display: submitted ? "" : "none" }}
        ></div>
      </section>

      <section className="content-reg">
        <form
          className="login-dialog"
          onSubmit={regHandler}
          style={{ display: submitted ? "none" : "" }}
        >
          <header className="login-title"></header>

          <div className="login-fields">
            <label className="field-row">
              <input
                type="text"
                className="text-input large"
                placeholder="Email"
                name="email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
              />
            </label>
            {error && !email && (
              <span className="error">*  email</span>
            )}

            <label className="field-row">
              <input
                type="password"
                className="text-input large"
                placeholder=""
                name="password"
                onChange={(e) => setPassword(e.target.value)}
                value={password}
              />
            </label>
            {error && !password && (
              <span className="error">*  </span>
            )}

            <label className="field-row">
              <input
                type="password"
                className="text-input large"
                placeholder=" "
                name="confirmPassword"
                onChange={(e) => setConfPassword(e.target.value)}
                value={confPassword}
              />
            </label>
            {error && !confPassword && (
              <span className="error">*  </span>
            )}
            {!passwordMatch && (
              <span className="error">  </span>
            )}
          </div>

          <footer className="login-footer">
            <Link type="button" className="button secondary" to="/login">
              
            </Link>

            <button type="submit" className="button primary">
              
            </button>
          </footer>
        </form>
      </section>
    </>
  );
}

export default Section;
