import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../hooks/useAuth";
import { useAppDispatch } from "../store/hooks";
import { logout } from "../store/user/userSlice";
import { removeTokenFromLocalStorage } from "../helpers/localStorage.helper";
import { toast } from "react-toastify";
import { instance } from "../api/axios.api";
import "boxicons/css/boxicons.min.css";

//  
import arrow from "../assets/arrow-out.png";
import avatar from "../assets/default-avatar.png";

function HeaderNoAuth({
  searchQuery,
  onSearchChange,
  onPerformSearch,
  onReset,
}) {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  
  //  state -  
  const [userAvatar, setUserAvatar] = useState("");
  const [loading, setLoading] = useState(true);
  const { isAuth } = useAuth();

  const getUserIdFromToken = () => {
    const token = localStorage.getItem("token");
    if (token) {
      try {
        const payload = JSON.parse(atob(token.split(".")[1]));
        return payload.id;
      } catch (error) {
        console.error("  :", error);
      }
    }
    return null;
  };

  const logoutHandler = () => {
    dispatch(logout());
    removeTokenFromLocalStorage("token");
    toast.success("   .");
    //    
    setUserAvatar("");
  };

  const userHandler = () => {
    navigate("/user");
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      onPerformSearch(searchQuery);
    }
  };

  //   
  useEffect(() => {
    const fetchUserAvatar = async () => {
      if (!isAuth) {
        setUserAvatar("");
        setLoading(false);
        return;
      }
      
      try {
        const userId = getUserIdFromToken();
        if (userId) {
          const response = await instance.get(`/user/${userId}`);
          //    base64
          setUserAvatar(response.data.avatar || "");
        }
      } catch (error) {
        console.error("  :", error);
        setUserAvatar("");
      } finally {
        setLoading(false);
      }
    };

    fetchUserAvatar();
  }, [isAuth,setUserAvatar]);

  //   ( )
  useEffect(() => {
    if (!onPerformSearch) return;
    const timeoutId = setTimeout(() => {
      onPerformSearch(searchQuery);
    }, 300);
    return () => clearTimeout(timeoutId);
  }, [searchQuery, onPerformSearch]);

  //   

  return (
    <>
      <header className="header">
        <div className="logo-container">
          <div className="left-group">
            <Link to="/">
              <i className="bx bx-globe bx-bounce" />
            </Link>

            <div className="search-wrapper">
              <input
                type="text"
                placeholder="  News"
                className="text-input medium with-button"
                value={searchQuery}
                onChange={(e) => onSearchChange(e.target.value)}
                onKeyPress={handleKeyPress}
              />
              <button className="reset-icon-button" onClick={() => onReset()}>
                <i className="bx bx-x" style={{ color: "rgba(0, 0, 0, 0.54)" }} />
              </button>
              <button
                className="search-icon-button"
                onClick={() => onPerformSearch(searchQuery)}
              >
                <i className="bx bx-search" style={{ color: "rgba(0, 0, 0, 0.54)" }} />
              </button>
            </div>
          </div>

          {isAuth ? (
            <div className="right-group">
              <Link to="/" onClick={logoutHandler}>
                
              </Link>
              <img className="reg-arrow" src={arrow} alt="" />
              
              {/* :   base64 -  ,   */}
              <img
                className="avatar-post"
                src={userAvatar || avatar}
                alt=""
                onClick={userHandler}
                onError={(e) => {
                  console.log("  ,  ");
                  e.target.src = avatar;
                }}
              />
            </div>
          ) : (
            <div className="right-group">
              <Link to="/registration" className="desktop-only">
                
              </Link>
              <img className="reg-arrow desktop-only" src={arrow} alt="" />
              <Link to="/login"></Link>
              <img className="reg-arrow" src={arrow} alt="" />
            </div>
          )}
        </div>
      </header>
      <hr className="hr-line" />
    </>
  );
}

export default HeaderNoAuth;