import React, { useState, useEffect } from "react";
import { instance } from "../api/axios.api";
import avatar from "../assets/default-avatar.png"; //  

const getAvatarUrl = (user) => {
  //onsole.log("User avatar data:", post.user?.avatar);
  return user?.avatar || avatar;
};

function ProfileEdit() {
  const API_BASE_URL = process.env.REACT_APP_API_URL;
  const [user, setUser] = useState({
    name: "",
    avatar: "",
    bio: "",
    email: "",
  });

  useEffect(() => {
    console.log("ENV :", {
      REACT_APP_API_URL: process.env.REACT_APP_API_URL,
      API_BASE_URL: API_BASE_URL,
    });
  }, []);

  const [postContent, setPostContent] = useState(""); // State   
  const [loading, setLoading] = useState(true);
  const [posting, setPosting] = useState(false); // State   
  const [saving, setSaving] = useState(false);
  const [uploading, setUploading] = useState(false);
  //    ID  
  const getUserIdFromToken = () => {
    const token = localStorage.getItem("token");
    if (token) {
      try {
        const payload = JSON.parse(atob(token.split(".")[1]));
        return payload.id;
      } catch (error) {
        console.error("  :", error);
      }
    }
    return null;
  };

  //  
  const handleSaveProfile = async () => {
    setSaving(true);
    try {
      const userId = getUserIdFromToken();
      if (userId) {
        //    ,   
        const updateData = {
          name: user.name,
          bio: user.bio,
          avatar: user.avatar,
        };

        await instance.patch(`/user/${userId}`, updateData);
        alert("  !");
      }
    } catch (error) {
      console.error(" :", error);
      console.error("URL :", error.config?.url);
      console.error(":", error.config?.method);
      alert("   ");
    } finally {
      setSaving(false);
    }
  };
  const handleSubmitPost = async (e) => {
    e.preventDefault();

    if (!postContent.trim()) {
      alert("    !");
      return;
    }

    setPosting(true);
    try {
      const response = await instance.post("/post/create", {
        content: postContent,
      });

      alert("  !");
      setPostContent(""); //   
      console.log(" :", response.data);
    } catch (error) {
      console.error("  :", error);
      alert("   ");
    } finally {
      setPosting(false);
    }
  };
  //  

  const handleAvatarUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    console.log("=== AVATAR UPLOAD DEBUG ===");
    console.log("API_BASE_URL:", API_BASE_URL);

    const userId = getUserIdFromToken();
    console.log("User ID from token:", userId);

    if (!userId) {
      alert(" ");
      return;
    }

    setUploading(true);
    try {
      const formData = new FormData();
      formData.append("avatar", file);

      // : instance    URL
      //   API_BASE_URL  
      const response = await instance.patch(
        `/user/${userId}/avatar`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      console.log("  :", response.data);

      if (response.data?.avatar) {
        //  
        setUser((prev) => ({
          ...prev,
          avatar: response.data.avatar,
        }));
        alert("  !");

        window.location.reload();
      }
    } catch (error) {
      console.error("  :", error);
      console.error("URL :", error.config?.url);
      console.error(":", error.response?.status);
      console.error(":", error.response?.data);

      alert(
        `   : ${
          error.response?.data?.message || error.message
        }`
      );
    } finally {
      setUploading(false);
      //  input
      event.target.value = "";
    }
  };
  //   
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const userId = getUserIdFromToken();
        if (userId) {
          //   endpoint /user/:id
          const response = await instance.get(`/user/${userId}`);
          setUser(response.data);
        }
      } catch (error) {
        console.error("  :", error);
      } finally {
        setLoading(false);
      }
    };

    fetchUserData();
  }, []);
  if (loading) return <div>...</div>;

  return (
    <section className="content">
      <div className="card profile">
        <header className="card-title">
          <div className="avatar-container">
            <img
              src={user?.avatar || avatar}
              alt=""
              className="avatar"
              onError={(e) => {
                console.error("     img ");
                console.error(
                  "SRC :",
                  user?.avatar?.substring(0, 100)
                );
                e.target.src = avatar;
              }}
              onLoad={() => console.log("    ")}
            />

            <label htmlFor="avatar-upload" className="avatar-upload-label">
              {uploading ? "" : ""}
              <input
                id="avatar-upload"
                type="file"
                accept="image/*"
                onChange={handleAvatarUpload}
                disabled={uploading}
                style={{ display: "none" }}
              />
            </label>
          </div>

          <div className="right-groop">
            <div className="form-group">
              <input
                type="text"
                value={user.name}
                className="text-input medium userName"
                onChange={(e) => setUser({ ...user, name: e.target.value })}
                placeholder="  "
              />
            </div>

            <div className="form-group">
              <label> :</label>
              <textarea
                value={user.bio}
                onChange={(e) => setUser({ ...user, bio: e.target.value })}
                placeholder="  "
                className="text-input large post"
                rows="4"
              />
            </div>
          </div>
        </header>

        {/*    */}
        <div className="form-group">
          <button
            type="button"
            onClick={handleSaveProfile}
            className="button primary"
            disabled={saving}
          >
            {saving ? "..." : " "}
          </button>
        </div>

        <div className="form-group">
          <label>Email:</label>
          <input
            type="email"
            value={user.email}
            disabled
            className="disabled-input"
          />
        </div>

        <form onSubmit={handleSubmitPost} className="post-form">
          <label className="field-row post">
            <textarea
              value={postContent}
              onChange={(e) => setPostContent(e.target.value)}
              className="text-input large post"
              placeholder="  "
              rows="3"
              disabled={posting}
            />
          </label>

          <button
            type="submit"
            className="button primary"
            disabled={posting || !postContent.trim()}
          >
            {posting ? "..." : " "}
          </button>
        </form>
      </div>
    </section>
  );
}

export default ProfileEdit;
