// hooks/useLikes.js
import { useState } from "react";
import { instance } from "../api/axios.api";
import { useAuth } from "./useAuth";

export function useLikes() {
  const { isAuth, user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  //  ID 
  const getUserId = () => {
    if (!user) return null;

    //       userId
    if (user.userId !== undefined && user.userId !== null) {
      return user.userId;
    }

    

    if (user.token) {
      try {
        const payload = user.token.split(".")[1];
        const decodedPayload = atob(payload);
        const decoded = JSON.parse(decodedPayload);
        return  decoded.userId || decoded.sub;
      } catch (err) {
        console.error("  :", err);
      }
    }

    console.error("   ID :", user);
    return null;
  };

  // ,    
  const isPostLiked = (post) => {
    if (!post || !user) return false;
    const userId = getUserId();
    return userId && post.likedByUserIds?.includes(userId);
  };

  //   
  const getLikesCount = (post) => {
    if (!post) return 0;
    return post.likedByUserIds?.length || 0;
  };

  //    /
  const handleLike = async (postId, currentPost) => {
    if (!isAuth || !user) {
      alert(",   ,   ");
      return { success: false, error: " " };
    }

    const userId = getUserId();
    if (!userId) {
      alert(" : ID   ");
      return { success: false, error: "ID   " };
    }

    setLoading(true);
    setError(null);

    try {
      // ,    
      const isLiked = currentPost?.likedByUserIds?.includes(userId);

      if (isLiked) {
        //  
        await instance.delete(`/post/${postId}/like`);
      } else {
        //  
        await instance.post(`/post/${postId}/like`);
      }

      //   
      const response = await instance.get(`/post/${postId}`);

      return {
        success: true,
        data: response.data,
        action: isLiked ? "unliked" : "liked",
      };
    } catch (error) {
      console.error("    :", error);
      setError(error.message);

      if (error.response?.status === 401) {
        return {
          success: false,
          error: " . ,  .",
        };
      } else {
        return {
          success: false,
          error: "   ",
        };
      }
    } finally {
      setLoading(false);
    }
  };

  return {
    // 
    loading,
    error,

    // 
    getUserId,
    isPostLiked,
    getLikesCount,
    handleLike,

    //  
    isAuth,
    user,

    // 
    canLike: isAuth && user && getUserId(),
  };
}
